const Objects = {
    /**
     * 
     * @param {Object} oldObj  
     * @param {Object} filler 
     * @returns {Object} 
     */
    cover(oldObj, filler) {
        for (let key of Object.keys(oldObj)) {
            if (filler[key] !== undefined) {
                oldObj[key] = filler[key];
            }
        }
        return oldObj;
    }
};

const Arrays = {

    /**
     * 
     * @param {Array<Object>} array 
     * @param {String} key ID
     * @returns {Array<String>} 
     */
    conflict(array, key) {
        const set = new Set();
        const result = new Set();
        for (let elem of array) {
            if (set.has(elem[key])) {
                result.add(elem[key]);
            }
            set.add(elem[key]);
        }
        return [...result];
    }
};

function deepCopy(obj) {
    if (Array.isArray(obj)) {
        return obj.map(deepCopy);
    } else if ('object' === typeof obj && obj !== null) {
        return Object.entries(obj).reduce((p, [k, v]) => (p[k] = deepCopy(v), p), {});
    } else {
        return obj;
    }
}

export {
    Objects,
    Arrays,
    deepCopy,
}
