<template>
    <!--  -->
    <div class="pa-5 d-flex">
        <div class="form d-flex flex-column align-center">
            <!--  -->
            <v-avatar 
                size="160"
                color="grey"
            >
                <img v-if="avatar" :src="avatarUrl" />

                <span v-else class="white--text text-h3">{{ name.slice(0, 2) }}</span>
            </v-avatar>

            <!--  -->
            <v-text-field
                label=""
                class="input flex-grow-0"
                messages=""
                placeholder=""
                v-model="name"
            />

            <!--  -->
            <ResSelector label="" messages="" v-model="avatar" />

            <!--  -->
            <ResSelector label="" v-model="pic" />
        </div>

        <!--  -->
        <div class="img-wrapper flex-grow-1">
            <img :src="picUrl" />
        </div>
    </div>
</template>

<script>
import ResSelector from "./ResSelector.vue";
import { mapMutations, mapState } from "vuex";
import { mutateWatcher } from '../utils/vue-util'


export default {
    name: "Role",

    components: {
        ResSelector,
    },

    props: {
        content: Object,
    },

    data() {
        return {
            name: this.content.name,
            avatar: this.content.avatar,
            pic: this.content.pic,
        };
    },

    watch: {
        ...mutateWatcher('content', 'name', 'avatar', 'pic'),
    },

    computed: {
        ...mapState(["data"]),

        id() {
            return this.content.id;
        },

        picUrl() {
            const res = this.data[this.pic];
            return res ? res.src : null;
        },

        avatarUrl() {
            const res = this.data[this.avatar];
            return res ? res.src : null;
        },
    },

    methods: {
        ...mapMutations(['updateData']),

        save() {
            this.updateData({
                id: this.id,
                name: this.name,
                avatar: this.avatar,
                pic: this.pic,
            });
        },
    },
};
</script>

<style lang="scss" scoped>
.img-wrapper {
    flex: 1;
    text-align: center;

    img {
        max-width: 100%;
        height: 100%;
        object-fit: contain;
        object-position: center;
    }
}

.form {
    > * {
        width: 20em;
        margin-top: 1.5em;
    }
}
</style>