<template>
  <div
    class="fill d-flex"
  >
      <!--  -->
      <v-navigation-drawer 
        class="role-list"
        permanent
      >
        <v-list>
          <v-list-item-group v-model="roleIndex">
            <v-list-item
              v-for="roleUid of roles"
              :key="roleUid"
            >
              <RAvatar
                class="mr-5"
                :src="getAvatarUrl(roleUid)"
                size="2em"
              />
              {{ data[roleUid].name }}
            </v-list-item>
          </v-list-item-group>
        </v-list>

        <!--  -->
        <div class="mx-5">
          <v-btn
            block
            @click="addRole"
          >
            <v-icon>mdi-plus</v-icon>
            
          </v-btn>
        </div>
      </v-navigation-drawer>

      <!--  -->
      <div
        v-if="role"
        class="role ma-5 d-flex flex-column align-center"
      >
        <div class="input d-flex align-start">
          <!--  -->
          <label class="mr-2">:</label>
          <v-input 
            class="input flex-grow-0"
            messages=""
            placeholder=""
          >
            <input 
              ref="nameInput"
              v-model="role.name"
              autofocus
            />
          </v-input>

          <v-spacer/>

          <!--  -->
          <label class="mr-2">:</label>
          <div class="input flex-grow-0">
            <v-autocomplete 
              class="ma-0"
              dense
              messages=""
              placeholder=""
              no-data-text=""
              v-model="role.avatar"
              :items="resList"
              item-text="name"
              item-value="uid"
            />
          </div>

          <RAvatar 
            class="ml-3"
            :src="avatarUrl"
            size="3em"
          />
        </div>

        <!--  -->
        <v-input class="input flex-grow-0">
          <label class="mr-2">:</label>
          <v-autocomplete 
            placeholder=""
            no-data-text=""
            v-model="role.pic"
            :items="resources.map(uid => ({...data[uid], uid}))"
            item-text="name"
            item-value="uid"
          />
        </v-input>
        
        <!--  -->
        <div class="img-wrapper">
          <img :src="picUrl"/>
        </div>

        <v-btn 
          color="red"
          class="white--text"
          @click="deleteRole"
        >
          <v-icon>mdi-minus</v-icon>
          
        </v-btn>
      </div>
  </div>
</template>

<script>
import RAvatar from '@/components/RAvatar.vue'

import state from '@/state.js'

const project = state.project;

export default {
  name: 'RoleManager',

  components: {
    RAvatar
  },

  data: () => ({
    roleIndex: null,
    data: project.data,
    resources: project.resources,
    roles: project.roles,
    role: null,
  }),

  watch: {
    roleIndex(index) {
      const uid = project.roles[index];
      this.role = uid ? project.data[uid] : null;
    },
  },

  computed: {
    resList() {
      return project.resources.map(uid => ({uid, ...this.data[uid]}));
    },

    picUrl() {
      const role = this.role;
      if (role && role.pic) {
        const res = project.data[role.pic];
        return res ? res.src : null;
      }
      return null;
    },

    avatarUrl() {
      const role = this.role;
      if (role && role.avatar) {
        const res = project.data[role.avatar];
        return res ? res.src : null;
      }
      return null;
    },
  },

  methods: {
    addRole() {
      project.roles.push(project.addData({
        name: '',
        avatar: null,
        pic: null,
      }));
      this.roleIndex = project.roles.length - 1;
    },

    deleteRole() {
      if (('number' === typeof this.roleIndex) && this.roleIndex >= 0) {
        const uid = project.roles[this.roleIndex];
        project.roles.splice(this.roleIndex, 1);
        delete project.data[uid];
        this.roleIndex = null;
      }
    },

    getAvatarUrl(uid) {
      const role = project.data[uid];
      if (role) {
        const res = project.data[role.avatar || role.pic];
        return res ? res.src : null;
      }
      return null;
    }
  }
}
</script>

<style lang="scss" scoped>


.fill {
  width: 100%;
  height: 100%;
}

.role {
  flex: 1;
  overflow-y: hidden;
}

.input {
  width: 50%;

  input {
    width: 100%;
    padding-top: 0;;
  }

  label {
    word-break: keep-all;
  }
}

.img-wrapper {
  width: 100%;
  height: 10rem;
  flex: 1;
  text-align: center;
  
  img {
    max-width: 100%;
    height: 100%;
    object-fit: contain;
    object-position: center;
  }
}

</style>