import Vue from 'vue'
import Vuex from 'vuex'
import { 
    cacheState, 
    compile, 
    downloadProject, 
    downloadScript,
    loadProjectFromFile,
    loadProjectFromCache,
} from './transfer'
import { 
    createResource, 
    createRole, 
    createChunk, 
    removeResource, 
    removeRole, 
    removeChunk, 
    updateData, 
} from './data-mutation'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {

    name: '', // 
    
    version: '1.0.0', // 

    authors: [], // 

    data: {
      'res-1': { id: 'res-1', src: null, name: 'Unnamed' },
      'role-1': { id: 'role-1', name: '', avatar: null, pic: null },
      'chunk-1': { id: 'chunk-1', title: '', subtitle: '', background: null, sections: [], exits: [] },
    }, // 

    resources: ['res-1'], // 

    roles: ['role-1'], // 

    chunks: ['chunk-1'], // 

    script: null, // 

  },
  mutations: {
    cacheState,
    compile,
    downloadProject,
    downloadScript,
    loadProjectFromFile,
    loadProjectFromCache,


    createResource,
    createRole,
    createChunk,

    removeResource,
    removeRole,
    removeChunk,

    updateData,

    updateBaseInfo(state, { name, version, authors }) {
      state.name = name || state.name;
      state.version = version || state.version;
      state.authors = authors || state.authors;
    },

  },
  actions: {
  },
})
